.over-about{

    width: 100%;
    height: 90vh;
}
.main-about{
display: flex;
width: 100%;
height: 70vh;
}


.left-about{
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.left-about img{
    width: 45vw;
    height: 66vh;
    
    object-fit: cover;
}
.head-about{
    padding-top:10px;
}
.text-about{
    margin-top: 3vw;
    margin-left: 1vw;
}
.vision{
    width: 25vw;
    text-align: center;
    height: 7vh;
    background-color:rgb(24, 24, 24);
    color: white;
    margin-bottom: 1vw;
}
.vision h2{
    font-size: 3vw;
}

.over-about h1{
    font-family: monument;
    font-size: 4vw;
    
    text-align: center;
}

.right-about{
    width: 50%;
    height: 100%;
}
.right-about p{
    width: 70%;
}

/* ================POLICY PAGE============================= */

.main-policy{
    display: flex;
    width: 100%;
    height: 70vh;
    margin-top: 5vw;
    }
    
    
    .left-policy{
        width: 50%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .left-policy img{
        width: 45vw;
        height: 66vh;
        
        object-fit: cover;
    }
    
    .text-policy{
        margin-top: 1vw;
        margin-left: 1vw;
    }
    .vision{
        width: 25vw;
        text-align: center;
        height: 7vh;
        background-color:rgb(24, 24, 24);
        color: white;
        margin-bottom: 2vw;
    }
    .vision h2{
        font-size: 3vw;
    }
    
    .right-policy{
        width: 50%;
        height: 100%;
    }
    .right-policy p{
        width:73%;
    }
    

@media (max-width:500px) {

    .main-about{
        flex-direction: column;

    }

    .left-about{

        width: 100%;


    }

    .left-about img{
width: 95vw;
    }

    .right-about{
width: 100%;
    }

    .vision{
width:100vw;
    }

    .over-about h1{
        font-size: 9vw;

    }

    .vision h2{
        font-size: 8vw;
    }

    .right-about p{
        width: 100%;
        margin-top: 3vw;
    }

    .main-policy{
flex-direction: column;

    }
    .left-policy{

        width: 100%;

    }
    .right-policy{

        width: 100%;
    }
    .right-policy p{
        width:100%;
        margin-top:6vw;
    }

    .left-policy img{

        width: 98vw;
    }
    
    
}