

.heading-product{
    height: 10vh;
    padding-top: 10px;
    background-color:#2874F0;

}
.over-product h1{
    text-align: center;
    font-family: monument;
    color: white;
    
}
.main-productdetail{
    width: 100%;
    height: 86vh;
    background-color:rgb(232, 230, 230);
display: flex;
}
.left-productdetail {
    width: 50%;
    height: 100%;

  }
  .photo-productdetail {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 70vh;
    object-fit: cover;
    border: 1px solid lightgrey;
  }

  .left-productdetail img {
    width: 40vw;
    height: 54vh;
    object-fit: cover;
}

.right-productdetail {
    width: 50%;
    height: 100%;

  }

  .right-productdetail h5 {
    font-size:18px;

    font-weight:bold;
    color: #26A541;

  }

  .right-productdetail h3 {
    font-size: 30px;
    color:black;
    font-weight: 500;
    font-family: monument;
    margin-top: 3vw;
    /* margin-bottom: 1vw; */
    letter-spacing: 2px;

  }
  .right-productdetail h6{
font-size: 24px;
font-weight: 600;
  }

  .right-productdetail h4 {
    font-size:20px;
    margin-bottom: 3px;
    letter-spacing: -1px;
    color: rgb(43, 42, 42);

  }
  .right-productdetail p{
    color: #2876F1;
    font-weight: bold;
  }

  .right-productdetail button{
    width: 12vw;
    border-radius: 6px;
    height: 7vh;
    color: white;
    background-color:#FF9F00;
    margin-top: 1vw;
    border: none;
    font-weight: 600;

  }

  .size h5 {
    font-size: 15px;
    font-weight: 600;
    color: #26A541;


  }
  .size{
    display: flex;
    align-items: center;
    margin-top: 6vw;
  }
  .size-box{
    width: 50px;
    font-size: 19px;
    height: 50px;
    margin-left: 20px;
    display: flex;
    border-radius: 4px;
    border: 2px solid gray;
    align-items: center;
    justify-content: center;
  }

  .size-box:hover{
    background-color: #2874F0;
    color: white;
  }

.similar-products{
    width: 100%;
    /* background-color:rgb(232, 230, 230); */
}






  .card-productdetail img {
    height: 45vh;
    width: 100%;
    object-fit: cover;
  }
  
  
  .card-productdetail {
    margin-top: 2vw;
    margin-left: 1vw;
    width: 26vw;
    position: relative;
    height: 72vh;
    background-color: rgb(225, 224, 224);
    overflow: hidden;
  
    margin-bottom: 1vw;
    border-radius: 5px;
    overflow: hidden;
  
  }
  
  .card-textdetail{
    margin-top: 8px;
    margin-left: 5px;
  }
  
  .hometext h4 {
    font-size: 21px;
    color: rgb(63, 62, 62);
    /* margin-bottom: 5px; */
  
  }
  
  .hometext h5 {
    font-size: 18px;
    margin-top: 6px;
    font-weight: 500;
    /* color: rgb(114, 112, 112); */
    color: rgb(78, 76, 76);
  }
  
  .text-back {
    height: 5vh;
    position: absolute;
    z-index: 9999;
    width: 100%;
    top: 93%;
    transition: all ease 0.4s;
  
    opacity: 0;
    /* background-color: lightgray; */
    /* background-color: #FF9F00; */
  }
  .adddetail-btn{
    background-color: #2874F0;
    color: white;
    width: 12vw;
    margin-left: 1vw;
    border-radius: 6px;
    font-size: 17px;
    font-weight: 600;
    height: 6vh;
    border: none;
    margin-top: 1vw;
    
  }

