.main-userprofile {
    width: 100%;
    display: flex;
    background-color: lightcyan;
}

.right-userprofile {
    width: 70%;
    background-color: #EEEEF0;
    /* background-color: lightpink; */
    display: flex;
    position: relative;

    align-items: center;
    justify-content: center;

}


.left-userprofile {
    width: 30%;
    height: 100%;

}

.form-userprofile {
    width: 40vw;
    padding-top: 10px;
    height: 62vh;

    border-radius: 7px;
    background-color: #266E8D;
    padding-left: 5vw;
    /* background-color: #F1931B; */

}

.right-userprofile h4 {
    position: absolute;
    top: 4%;
    font-size: 2.5vw;
    font-weight: 600;
}

.input-profile {
    width: 30vw;
    height: 6vh;
    margin-top: 10px;
    border: none;
    outline: none;
    border-radius: 5px;
}

.update-userprofile {
    width: 30vw;
    height: 6vh;
    border: none;
    color: white;
    font-weight: 600;
    font-size: 20px;
    border-radius: 6px;
    background-color: #21D0B4;
}


@media (max-width:500px) {

    .main-userprofile {

        flex-direction: column;
    }


    .left-userprofile {
        width: 100%;
    }

    .right-userprofile h4 {

        top: -12%;
        font-size:10vw;
    }
    
    .right-userprofile {
width: 100%;
    }

    .form-userprofile {
        width: 90vw;
        height: 70vh;
    }

    .input-profile {
width: 80vw;
height: 8vh;
    }

    .update-userprofile {
margin-left: 23vw;
    }
}